h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
}

@media (width >= 640px) {
  .page__content {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    display: grid;
  }
}

@media (width >= 640px) and (width >= 640px) {
  .page__content {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }
}

@media (width >= 640px) and (width >= 1280px) {
  .page__content {
    grid-template-columns: repeat(12, 1fr);
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 10;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.page__overlay {
  z-index: 5;
  opacity: 0;
  pointer-events: none;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

